.app {
    display: grid;
    grid-template-columns: [col-1] 20% [col-2] auto [col-3] 10% [col-4];
    grid-template-rows: [row-1] auto [row-2] auto [row-3] auto [row-4] auto [row-5] ;
    grid-template-areas:
        "info main legend"
        "charts main ."
        ". main ."
        ". main ."
        ". main .";

    &__content {
        grid-area: main;
        width: 95%;
        margin: auto;
        margin-top: 3rem;
        padding: 2rem;
        background-color: rgb(245, 239, 239);
        box-shadow: 1px 1px 1px 2px rgb(217, 217, 217);
    }

    &__legend {
        grid-area: legend;
        width: 95%;
        margin: auto;
        margin-top: 3rem;
        padding: 2rem;
        background-color: rgb(245, 239, 239);
        box-shadow: 1px 1px 1px 2px rgb(217, 217, 217);
    }

    &__info {
        grid-area: info;

        width: 90%;
        padding: 2rem;
        margin: auto;
        margin-top: 3rem;
        background-color: rgb(245, 239, 239);
        box-shadow: 1px 1px 1px 2px rgb(217, 217, 217);
    }

    &__charts {
        grid-area: charts;

        width: 90%;
        padding: 2rem;
        margin: auto;
        margin-top: 3rem;
        background-color: rgb(245, 239, 239);
        box-shadow: 1px 1px 1px 2px rgb(217, 217, 217);
    }
}